import styled, { keyframes } from 'styled-components';
import { motion } from "framer-motion";
import { forwardRef, useState, useRef } from 'react';
import React from 'react';

/* */
const AboutWrap = styled.div`
    padding : 50px 0;
`
const AboutmeContainer = styled.div`
margin : 50px 0 50px 0;
display : flex;
justify-content: center;
`
const Content1 = styled(motion.div)`
&& > .text_about {
display : flex;
}
&& .text_about > .subhead_about {
margin : 15px 0 5px 50px;
font-size : 18px;
font-weight : 700;
}  && .text_about > .main_content {
margin : 15px 50px 5px 12px;
font-size : 18px;
font-weight : 700;
}
`
const Content2 = styled(motion.div)`
font-size : 16px; font-weight : 700;
margin : 20px 0;
width : 750px;
line-height : 36px;
`
/* */



const Aboutme = forwardRef(({aboutRef},ref) => {

   
    return (
        <AboutWrap ref={aboutRef}>
            <h1>About me</h1>
            <AboutmeContainer>
                <Content1>
                    <div className='text_about'><div className='subhead_about'> :</div><div className='main_content'></div></div>
                    <div className='text_about'><div className='subhead_about'> :</div><div className='main_content'>  </div></div>
                    <div className='text_about'><div className='subhead_about'>  :</div><div className='main_content'>IV   </div></div>
                </Content1>
                <Content2>
                    .   .      .
                      Spring framework(MVC )     
                         2  .
                </Content2>
            </AboutmeContainer>
        </AboutWrap>
    )
}) 

export {Aboutme};